.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.pegado {
  display: flex;
  flex-wrap: wrap;
}
.fixed {
  table-layout: fixed;
  border: 1px dashed gainsboro;
}
.tbtitle {
  background-color: crimson;
  color: white;
}

.scrolling-wrapper {
  overflow-x: hidden;
  overflow-y: scroll;
  white-space: nowrap;
}

.hscrolling-wrapper {
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  overflow-x: scroll;
  overflow-y: scroll;
  height: 60%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.ancho {
  height: 600px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
